/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lexend", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.seccion {
  padding: 2rem 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

a {
  text-decoration: none;
}

[class$=__heading] {
  margin: 2rem 0;
}

.header {
  background-image: url(../img/header.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  padding: 3rem 0 20rem 0;
}
.header__busqueda {
  margin-top: 5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .header__busqueda {
    margin-top: 30rem;
  }
}
.header__busqueda-boton {
  background-color: #FFFFFF;
  padding: 2rem 4rem;
  color: #e8505b;
  margin-top: 1rem;
  border-radius: 3rem;
  font-size: 2rem;
  font-weight: 700;
  display: block;
  transition-property: box-shadow;
  transition-duration: 0.3s;
}
@media (min-width: 768px) {
  .header__busqueda-boton {
    display: inline-block;
  }
}
.header__busqueda-boton:hover {
  box-shadow: 0px 6px 20px rgba(255, 255, 255, 0.4);
}

.barra {
  display: none;
}
@media (min-width: 912px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.barra__nombre {
  color: #FFFFFF;
  font-weight: 900;
  border-radius: 1rem;
  margin: 0;
  text-shadow: 1px 2px 10px rgb(189, 30, 89);
  transition-property: color text-shadow;
  transition-duration: 0.3s;
}
.barra__nombre:hover {
  color: #e8505b;
  text-shadow: 2px 1px 4px #000;
}
@media (min-width: 768px) {
  .barra__navegacion {
    display: flex;
    gap: 4rem;
  }
}
.barra__link {
  color: #FFFFFF;
  text-shadow: 1px 1px 10px #000;
  transition-property: color;
  transition-duration: 0.3s;
  border-bottom: 5px solid transparent;
}
.barra__link:hover {
  color: rgb(189, 30, 89);
  text-shadow: 1px 1px 10px #e8505b;
  border-bottom: 5px solid #e8505b;
}

.formulario {
  margin: 5rem auto 2rem auto;
  background-color: #FFFFFF;
  border-radius: 5rem;
  box-shadow: 0px 3px 7px 0px #686868;
  padding: 3rem 0;
}
@media (min-width: 1024px) {
  .formulario {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 90rem;
    padding: 0;
  }
}
.formulario__campo {
  padding: 1rem 2rem;
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .formulario__campo:nth-child(1) {
    padding-left: 4rem;
  }
}
.formulario__campo:hover {
  background-color: #f0f0f0;
  border-radius: 5rem;
  box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.4);
}
.formulario__label {
  font-size: 1.2rem;
  font-weight: 900;
}
.formulario__input {
  border: none;
  width: 100%;
  color: #686868;
}
.formulario__submit {
  display: block;
  height: 4rem;
  border: none;
  border-radius: 2rem;
  background-image: radial-gradient(circle, rgb(255, 56, 92) 0%, rgb(189, 30, 89) 100%);
  padding: 0 3rem;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 1.2rem;
}

.destinos__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .destinos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .destinos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1440px) {
  .destinos__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

.destino {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  gap: 2rem;
}
.destino__imagen {
  width: 14rem;
}
.destino__nombre, .destino__distancia {
  margin: 0;
}

.lugares__grid {
  display: grid;
  grid-template-columns: repeat(4, 30rem);
  column-gap: 4rem;
  overflow-y: hidden;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
}
.lugares__grid > * {
  scroll-snap-align: center;
}
@media (min-width: 768px) {
  .lugares__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .lugares__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

.lugar__imagen {
  border-radius: 1rem;
}
.lugar__nombre {
  font-size: 1.8rem;
  text-align: center;
}

.anfitrion {
  color: #FFFFFF;
  border-radius: 1rem;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  row-gap: 10rem;
  padding: 5rem;
  background-image: linear-gradient(to bottom, #000 0%, #000 50%, transparent 50%), url(../img/anfitrion.jpg);
  background-size: 100%, 62rem;
  background-position: center, 67% 95%;
  background-repeat: no-repeat;
}
@media (min-width: 360px) {
  .anfitrion {
    background-size: 100%, 64rem;
    background-position: center, 74% 110%;
  }
}
@media (min-width: 480px) {
  .anfitrion {
    background-size: 100%, 74rem;
    background-position: center, center 200%;
  }
}
@media (min-width: 540px) {
  .anfitrion {
    background-size: 100%, 68rem;
  }
}
@media (min-width: 768px) {
  .anfitrion {
    grid-template-rows: unset;
    background-image: linear-gradient(to right, #000 0%, #000 45%, transparent 55%), url(../img/anfitrion.jpg);
    background-size: 100%, auto;
    background-position: center, 20% 20%;
    padding: 15rem 5rem;
  }
}
@media (min-width: 912px) {
  .anfitrion {
    background-position: center, -40% 20%;
  }
}
@media (min-width: 1024px) {
  .anfitrion {
    background-position: center, 100% 20%;
  }
}
@media (min-width: 1440px) {
  .anfitrion {
    background-position: center, right 20%;
  }
}
@media (min-width: 768px) {
  .anfitrion__contenido {
    max-width: 50%;
  }
}
.anfitrion__texto {
  color: #FFFFFF;
}
.anfitrion__enlace {
  background-color: #FFFFFF;
  padding: 2rem 4rem;
  color: #000;
  margin-top: 1rem;
  border-radius: 3rem;
  font-size: 2rem;
  font-weight: 700;
  display: block;
  border-radius: 1rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  text-align: center;
  transition-property: box-shadow;
  transition-duration: 0.3s;
}
@media (min-width: 768px) {
  .anfitrion__enlace {
    display: inline-block;
  }
}
.anfitrion__enlace:hover {
  box-shadow: 0px 6px 20px rgba(255, 255, 255, 0.4);
}

.experiencias__listado {
  display: grid;
  grid-template-columns: repeat(3, 30rem);
  column-gap: 2rem;
  overflow-y: hidden;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
}
.experiencias__listado > * {
  scroll-snap-align: center;
}
@media (min-width: 768px) {
  .experiencias__listado {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.experiencia__imagen {
  border-radius: 1rem;
}
.experiencia__titulo {
  font-weight: 700;
  font-size: 2rem;
}
.experiencia__descripcion {
  font-size: 1.8rem;
}

.footer {
  background-color: #f3f3f3;
  border-top: 1px solid #cecece;
  padding-top: 5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
    justify-content: center;
  }
}
@media (min-width: 1024px) {
  .footer__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (min-width: 768px) {
  .footer__widget {
    width: 25rem;
  }
}
@media (min-width: 1024px) {
  .footer__widget {
    width: 100%;
  }
}
.footer__heading {
  text-transform: uppercase;
  font-size: 1.2rem;
}
.footer__enlace {
  display: block;
  color: #686868;
  font-size: 1.4rem;
  margin-bottom: 1rem;
  padding: 1rem;
  border-radius: 2rem;
  transition-property: background-color;
  transition-duration: 0.3s;
}
.footer__enlace:hover {
  background-color: #e1e1e1;
  border-radius: 2rem;
}
.footer__barra {
  display: flex;
  flex-direction: column-reverse;
  border-top: 1px solid #cecece;
  margin-top: 2rem;
  padding: 2rem 0;
}
@media (min-width: 1024px) {
  .footer__barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.footer__copyright {
  border-top: 1px solid #cecece;
  padding: 2rem 0 0 0;
  font-size: 1.4rem;
  color: #686868;
  margin: 0;
}
@media (min-width: 1024px) {
  .footer__copyright {
    border-top: none;
    padding-top: 0;
  }
}
.footer__barranav {
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .footer__barranav {
    flex-direction: row;
    justify-content: space-between;
  }
}
@media (min-width: 1024px) {
  .footer__barranav {
    margin-bottom: 0;
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .footer__enlace--barra {
    margin-bottom: 0;
  }
}
.footer__enlace--barra::before {
  content: " - ";
  margin-right: 0.5rem;
}